const isDev = !(process.env.NODE_ENV === 'production')

const list = [
    {
        name: '/main/defalut/', // app name registered
        // eslint-disable-next-line
        activeRule: (location: any) => {
            return location.pathname === '/main/default/'
        },
        devEntry: `//localhost:8099`,
        container: '#container',
        entry: '',
    },
    {
        name: '/main/vue2/', // app name registered
        activeRule: '/main/vue2/',
        devEntry: `//localhost:8001`,
        container: '#container',
        entry: '',
    },
    {
        name: '/main/vue3/', // app name registered
        activeRule: '/main/vue3/',
        devEntry: `//localhost:8002`,
        container: '#container',
        entry: '',
    },
    {
        name: '/main/react/', // app name registered
        activeRule: '/main/react/',
        devEntry: `//localhost:8003`,
        container: '#container',
        entry: '',
    },
    {
        name: '/main/vue2hash/', // app name registered
        activeRule: '/main/vue2hash/',
        devEntry: `//localhost:8010`,
        container: '#container',
        entry: '',
    },
    {
        name: '/main/vue3hash/', // app name registered
        activeRule: '/main/vue3hash/',
        devEntry: `//localhost:8011`,
        container: '#container',
        entry: '',
    },
]

// eslint-disable-next-line @typescript-eslint/no-explicit-any
export default function getRegisterApp(): any {
    // eslint-disable-next-line
    return list.map((item: any) => {
        item.entry = isDev ? item.devEntry : item.name
        return item
    })
}